.about__container {
  display: flex;

  background-color: rgb(249, 244, 244);

  padding: 0rem 15rem;
  /* height: 105vh; */
  height: auto;
  flex-direction: column;
  /* justify-content: center;
    align-items: center; */
  /* flex-direction: row; */
}

.about__wrapper {
  /* display: flex; */
  flex-direction: column;
  justify-content: center;
  flex-wrap: wrap;
  display: grid;
  grid-template-columns: 1fr 1fr;
  height: 70vh;
  grid-gap: 10rem;
}

.about__photo-wrapper {
  height: 70vh;
  width: auto;
}

.about__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 5px;
}

.about__text-title {
  font-size: 6rem;

  color: #6b5b31;
  padding: 2rem 0;
  font-family: "Dancing Script";
  font-weight: bold;
}

.about__first-p {
  font-size: 2.5rem;
  font-weight: 500;
  padding-bottom: 2rem;
}

.about__first-p-name {
  font-family: "Dancing Script";
  font-size: 4rem;
}

.about__text {
  font-size: 2rem;
  line-height: 2;
  margin-bottom: 2rem;
  color: #4d4d4d;
}

.about-search__wrapper {
  padding-bottom: 5rem;
}
